// @file
// Base styles

html {
  box-sizing: border-box;
  font-size: $base-font-size;
  line-height: $base-line-height;
  min-height: 100%;
  text-size-adjust: 100%;
}

body {
  color: $white;
  font-family: $base-font-family;
  margin: 0;
  overflow-x: hidden;
  padding: 0;
  transition: background-color 1s ease-in-out;
  word-wrap: break-word;

  &.purple-dark {
    background-color: $purple-dark;
  }

  &.magenta-dark {
    background-color: $magenta-dark;
  }

  &.no-scroll {
    overflow-y: hidden;
  }

  &[data-color='purple-dark'] {
    background-color: $purple-dark;
  }

  &[data-color='magenta-dark'] {
    background-color: $magenta-dark;
  }

  &[data-color='magenta'] {
    background-color: $magenta;
  }
  
  &[data-color='magenta-2'] {
    background-color: $magenta-2;
  }

  &[data-color='pink'] {
    background-color: $pink;
  }
  
  &[data-color='purple'] {
    background-color: $purple;
  }

  [data-color='purple-dark'] {
    background-color: $purple-dark;
  }
  
  &[data-color='orange'] {
    background-color: $orange;
  }

  &[data-color='orange-light'] {
    background-color: $orange-light;
  }
}

::selection {
  color: $magenta;
  background: $white;
}

::-moz-selection {
  color: $magenta;
  background: $white;
}

%hN {
  @extend %header-font-family !optional;
  color: $white;
  font-weight: 700;
  hyphens: none;
  line-height: 1.1;
  margin: 0 0 rem($vertical-spacing);
  text-rendering: optimizeLegibility;

  @media print {
    & {
      orphans: 3;
      page-break-after: avoid;
      widows: 3;

      &::after {
        display: inline-block;
      }
    }
  }
}

h1,
%h1 {
  @extend %hN;
  font-size: $font-size-h2;
  margin-bottom: 0.5em;

  @include bp-medium {
    font-size: $font-size-h1;
  }

  &::before,
  &::after {
    display: none;
  }
}

h2,
%h2 {
  @extend %hN;
  font-size: $font-size-h3;

  @include bp-medium {
    font-size: $font-size-h2;
  }
}

h3,
%h3 {
  @extend %hN;
  font-size: $font-size-lg;

  @include bp-medium {
    font-size: $font-size-h3;
  }
}

h4,
%h4 {
  @extend %hN;
  font-size: $font-size-h5;

  @include bp-medium {
    font-size: $font-size-h4;
  }
}

h5,
%h5 {
  @extend %hN;
  font-size: $font-size-h6;

  @include bp-medium {
    font-size: $font-size-h5;
  }
}

h6,
%h6 {
  @extend %hN;
  font-size: $font-size-h6;
}

h1, h2, h3, h4, h5, h6 {
  // Remove top margin for adjacent subheadings.
  
}

p {
  margin: 0 0 1rem;

  @media print {
    & {
      orphans: 3;
      widows: 3;
    }
  }
}

hr {
  border: solid 1px $white;
}

// section {}

a {
  color: $white;
  
  text-decoration: none;
  
  &:hover,
  &:focus {
    text-decoration: none;
  }
}

// main {}

img {
  max-width: 100%;
}